import React from 'react';
import './font.css';

function Font({ font }) {
	const handleCls = () => {
		if (font === 'lotus') {
			return `Lotus`;
		}
		if (font === 'yekan') {
			return `Yekan`;
		}
		if (font === 'samim') {
			return `Samim`;
		}
		if (font === 'shabnam') {
			return `Shabnam`;
		}
		if (font === 'mitra') {
			return `Mitra`;
		}
		if (font === 'sahel') {
			return `Sahel`;
		}
		if (font === 'nazanin') {
			return `Nazanin`;
		}
		if (font === 'vazir') {
			return `Vazir`;
		}
	};
	const cls = handleCls();
	return (
		<div className={`${cls} container pb-4 pt-4`}>
			<h1 className="text-center" style={{ color: 'grey' }}>
				{cls}
			</h1>
			<h1 style={{ fontWeight: 'bold' }} className="text-center">
				فونت بزرگ
			</h1>
			<h2 style={{ fontWeight: 'bold' }} className="text-center">
				Lorem ipsum dolor sit amet
			</h2>
			<p dir="rtl" style={{ fontSize: '20px' }} className="text-center">
				لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ و با استفاده از طراحان گرافیک است.
			</p>
			<h2 style={{ fontWeight: 'bold' }} className="text-center">
				۱ ۲ ۳ ۴ ۵ ۶ ۷ ۸ ۹ ۰{' '}
			</h2>
			<div className="d-flex justify-content-center">
				<code>
					{`
               <link href='https://cdn.fontcdn.ir/Font/Persian/${cls}/${cls}.css' rel='stylesheet' type='text/css'>
                `}
				</code>
			</div>
			<div className="d-flex justify-content-center mt-2">
				<code>
					{`
                font-family : '${cls}';
                `}
				</code>
			</div>
		</div>
	);
}

export default Font;
